<template>
  <div>
    <el-dialog
      @opened="openDialog"
      :width="dialogWidth"
      :title="title"
      :visible.sync="showEditDialog"
      :show-close="false"
      :close-on-click-modal="false"
    >
      <el-form
        :model="forms"
        :rules="rules"
        ref="editForms"
        class="edit-forms"
        label-position="left"
        :label-width="labelWidth"
      >
        <el-form-item label="类型名称" prop="name">
          <el-input v-model="forms.name" placeholder="类型名称"></el-input>
        </el-form-item>
        <el-form-item label="父类型名称" prop="parentName">
          <el-input v-model="forms.parentName" placeholder="父类型名称" readonly></el-input>
        </el-form-item>
        <el-form-item label="层级" prop="level">
          <el-input v-model="forms.level" readonly></el-input>
        </el-form-item>
        <el-form-item label="第三级目录图标" v-if="forms.level=='L3'">
          <el-upload
            class="avatar-uploader"
            action
            :show-file-list="false"
            :http-request="uploadImage"
          >
            <img v-if="forms.picUrl" :src="forms.picUrl" class="avatar" />
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="$emit('close')">取 消</el-button>
        <el-button type="primary" @click="submit">确认</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { Required } from "@/utils/validateRules";
import { editCategory, uploadImage } from "@/api/goods/goodsManage";
import { fillerLeft, resetObject } from "@/utils/common";

export default {
  props: {
    title: {
      type: String,
      default: "编辑"
    },
    dialogWidth: {
      type: String,
      default: "700px"
    },
    labelWidth: {
      type: String,
      default: "120px"
    },
    showEditDialog: Boolean
  },
  data() {
    return {
      forms: {
        id: "",
        name: "",
        parentName: "",
        picUrl: "",
        level: "",
        pid: 0
      },
      rules: {
        name: Required,
        parentName: Required,
        picUrl: Required
      }
    };
  },
  methods: {
    submit() {
      this.$refs.editForms.validate(valid => {
        if (valid) {
          if (this.forms.level == "L3" && !this.forms.picUrl) {
            this.$message.error("第三级目录图标没有上传");
            return;
          }
          editCategory(this.forms).then(data => {
            if (data.code == 0) {
              this.$message.success("更新成功！");
              this.$emit("success");
              return;
            }
            this.$message.error(data.msg);
          });
          return;
        }
        this.$message.error("请按照提示正确填写内容！");
        return false;
      });
    },
    //任务图标文件上传成功后
    async uploadImage(fileObj) {
      this.forms.picUrl = await this.upload(fileObj);
    },
    removeImage(item) {
      item.taskStepPicUrl = "";
    },
    async upload(fileObj) {
      if (!this.beforeAvatarUpload(fileObj.file)) return;
      let formData = new FormData();
      formData.set("file", fileObj.file);
      const data = await uploadImage(formData);
      if (data.code != 0) {
        this.$message.error(data.msg);
        return "";
      }
      console.log(data.data);
      return data.data;
    },
    //文件上传前
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg" || file.type === "image/png";
      const isLt2M = file.size / 1024 / 1024 < 2;
      if (!isJPG) {
        this.$message.error("上传头像图片只能是 JPG 格式!");
      }
      if (!isLt2M) {
        this.$message.error("上传头像图片大小不能超过 2MB!");
      }
      return isJPG && isLt2M;
    },
    openDialog() {},
    add(level, pid, parentName) {
      this.$set(this.forms, "id", null);
      this.$set(this.forms, "picUrl", null);
      this.$set(this.forms, "level", level);
      this.$set(this.forms, "pid", pid);
      this.$set(this.forms, "parentName", parentName);
      this.$set(this.forms, "name", null);
    },
    update(id, data) {
      //可以外部填充回写做编辑用，也可以请求详情接口填充表单
      this.$set(this.forms, "id", id);
      fillerLeft(this.forms, data);
    }
  }
};
</script>
