<template>
  <div>
    <ToolBar>
      <div>
        <el-input
          placeholder="类型名称"
          size="small"
          style="width: 140px"
          v-model="searchParams.name"
          clearable
        ></el-input>
        <el-input
          placeholder="父类型名称"
          size="small"
          style="width: 140px"
          v-model="searchParams.parentName"
          clearable
        ></el-input>
        <el-select
          v-model="searchParams.level"
          clearable
          style="width: 140px"
          placeholder="类型"
          size="small"
        >
          <el-option v-for="(k,v,index) in categoryLevelEnum" :key="index" :label="k" :value="v"></el-option>
        </el-select>

        <el-button type="success" size="small" @click="refresh()">查询</el-button>
        <el-button type="warning" size="small" @click="clearSearchParams()">重置</el-button>
      </div>
      <div>
        <el-button type="primary" size="small" @click="add('L0',0,'根目录')">添加</el-button>
      </div>
    </ToolBar>
    <el-table
      :data="tableData"
      border
      style="width: 100%"
      :row-style="{height:'8px'}"
      :cell-style="{padding:'0px 0px'}"
    >
      <el-table-column align="center" prop="name" label="类型"></el-table-column>
      <el-table-column align="center" prop="parentName" label="父类型"></el-table-column>
      <el-table-column align="center" prop="level" label="层级" :formatter="levelFormatter"></el-table-column>
      <el-table-column align="left" label="操作">
        <div slot-scope="s">
          <el-button
            v-if="s.row.level!='L3'"
            type="primary"
            size="small"
            @click="add(s.row.level,s.row.id,s.row.parentName)"
          >添加子目录</el-button>
          <el-button type="primary" size="small" @click="edit(s.row)">编辑</el-button>
          <el-button type="danger" size="small" @click="removeItem(s.row)">删除</el-button>
        </div>
      </el-table-column>
    </el-table>
    <Pagination
      :params="searchParams"
      :requestFunc="getCategoryList"
      ref="pagination"
      @returnData="returnData"
    />
    <CategoryEdit
      :title="titie"
      ref="edit"
      :showEditDialog="showEditDialog"
      @close="showEditDialog = false"
      @success="refresh"
    />
  </div>
</template>

<script>
import { getCategoryList, deleteCategoryList } from "@/api/goods/goodsManage";
import { getDateList } from "@/utils/date";
import { resetObject } from "@/utils/common";
import { CategoryLevelEnum } from "@/utils/Immutable";
import CategoryEdit from "./CategoryEdit";
export default {
  components: { CategoryEdit },
  data() {
    return {
      searchParams: {
        name: "",
        parentName: "",
        level: ""
      },
      getCategoryList: getCategoryList,
      titie: "添加",
      categoryLevelEnum: CategoryLevelEnum,
      showEditDialog: false,
      tableData: []
    };
  },
  created() {},
  methods: {
    levelFormatter(a, b, v) {
      return this.categoryLevelEnum[v];
    },
    edit(row) {
      this.titie = "修改";
      this.showEditDialog = true;
      this.$refs.edit.update(row.id, row);
    },
    add(level, pid, parentName) {
      this.titie = "添加";
      this.showEditDialog = true;
      level = level.charAt(0) + (level.charAt(1) * 1 + 1);
      this.$refs.edit.add(level, pid, parentName);
    },
    removeItem(item) {
      this.$confirm(
        "此操作将永久删除该类目吗？一般不允许删除, 是否继续?",
        "提示",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        }
      )
        .then(() => {
          deleteCategoryList(item.id).then(res => {
            if (res.code == 0) {
              this.$message.success("删除成功!");
              this.refresh();
              return;
            }
            this.$message.error(res.msg);
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    clearSearchParams() {
      resetObject(this.searchParams);
      this.refresh();
    },
    returnData(data) {
      this.tableData = data;
    },
    refresh() {
      this.$refs.pagination.Refresh(); //分页刷新
      this.showEditDialog = false;
    }
  }
};
</script>
<style lang="scss">
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 60px;
  height: 60px;
  line-height: 60px;
  text-align: center;
}
.avatar {
  width: 80px;
  height: 80px;
  display: block;
}
.my-icon {
  font-size: 20px;
  position: absolute;
  left: 60px;
  top: 0px;
  color: red;
}
</style>